// ** React Imports
import { Fragment, lazy } from 'react'
import { Navigate } from 'react-router-dom'
// ** Layouts
import BlankLayout from '@layouts/BlankLayout'
import VerticalLayout from '@src/layouts/VerticalLayout'
import HorizontalLayout from '@src/layouts/HorizontalLayout'
import LayoutWrapper from '@src/@core/layouts/components/layout-wrapper'

// ** Route Components
import PublicRoute from '@components/routes/PublicRoute'
import PrivateRoute from '@components/routes/PrivateRoute'

// ** Utils
import { isObjEmpty } from '@utils'

const getLayout = {
  blank: <BlankLayout />,
  vertical: <VerticalLayout />,
  horizontal: <HorizontalLayout />
}

// ** Document title
const TemplateTitle = '%s - Vuexy React Admin Template'

// ** Default Route
const DefaultRoute = '/movies'

const Games = lazy(() => import('../../views/games/Games'))
const AddGames = lazy(() => import('../../views/games/AddGames'))
const ViewGames = lazy(() => import('../../views/games/ViewGames'))

const Movies = lazy(() => import('../../views/movies/Movies'))
const AddMovies = lazy(() => import('../../views/movies/AddMovies'))
const ViewMovies = lazy(() => import('../../views/movies/ViewMovies'))

const Songs = lazy(() => import('../../views/songs/Songs'))
const AddSongs = lazy(() => import('../../views/songs/AddSongs'))
const ViewSongs = lazy(() => import('../../views/songs/ViewSongs'))

const Subscription = lazy(() => import('../../views/subscription/PricingCards'))
const Payment = lazy(() => import('../../views/subscription/Payment'))
const AddContent = lazy(() => import('../../views/content/AddContent'))
const ChangePassword = lazy(() => import('../../views/account-settings/SecurityTabContent'))
const Login = lazy(() => import('../../views/auth/Login'))
const Register = lazy(() => import('../../views/auth/Register'))
const ForgotPassword = lazy(() => import('../../views/auth/ForgotPassword'))
const Error = lazy(() => import('../../views/Error'))

// ** Merge Routes
const Routes = [
  {
    path: '/',
    index: true,
    element: <Navigate replace to={DefaultRoute} />
  },
  {
    path: '/payment/:type',
    element: <Payment />
  },
  {
    path: '/add-content',
    element: <AddContent />
  },
  {
    path: '/subscription',
    element: <Subscription />
  },
  {
    path: '/games',
    element: <Games />
  },
  {
    path: '/games-add',
    element: <AddGames />
  },
  {
    path: '/games-view/:id',
    element: <ViewGames />
  },
  {
    path: '/games-edit/:id',
    element: <AddGames />
  },
  {
    path: '/movies',
    element: <Movies />
  },
  {
    path: '/movies-add',
    element: <AddMovies />
  },
  {
    path: '/movies-view/:id',
    element: <ViewMovies />
  },
  {
    path: '/movies-edit/:id',
    element: <AddMovies />
  },
  {
    path: '/songs',
    element: <Songs />
  },
  {
    path: '/songs-add',
    element: <AddSongs />
  },
  {
    path: '/songs-view/:id',
    element: <ViewSongs />
  },
  {
    path: '/songs-edit/:id',
    element: <AddSongs />
  },
  {
    path: '/change-password',
    element: <ChangePassword />
  },
  {
    path: '/login',
    element: <Login />,
    meta: {
      layout: 'blank',
      publicRoute: true,
      restricted: true
    }
  },
  {
    path: '/register',
    element: <Register />,
    meta: {
      layout: 'blank',
      publicRoute: true,
      restricted: true
    }
  },
  {
    path: '/forgot-password',
    element: <ForgotPassword />,
    meta: {
      layout: 'blank',
      publicRoute: true,
      restricted: true
    }
  },
  {
    path: '/error',
    element: <Error />,
    meta: {
      layout: 'blank'
    }
  }
]

const getRouteMeta = route => {
  if (isObjEmpty(route.element.props)) {
    if (route.meta) {
      return { routeMeta: route.meta }
    } else {
      return {}
    }
  }
}

// ** Return Filtered Array of Routes & Paths
const MergeLayoutRoutes = (layout, defaultLayout) => {
  const LayoutRoutes = []

  if (Routes) {
    Routes.filter(route => {
      let isBlank = false
      // ** Checks if Route layout or Default layout matches current layout
      if (
        (route.meta && route.meta.layout && route.meta.layout === layout) ||
        ((route.meta === undefined || route.meta.layout === undefined) && defaultLayout === layout)
      ) {
        let RouteTag = PrivateRoute

        // ** Check for public or private route
        if (route.meta) {
          route.meta.layout === 'blank' ? (isBlank = true) : (isBlank = false)
          RouteTag = route.meta.publicRoute ? PublicRoute : PrivateRoute
        }
        if (route.element) {
          const Wrapper =
            // eslint-disable-next-line multiline-ternary
            isObjEmpty(route.element.props) && isBlank === false
              ? // eslint-disable-next-line multiline-ternary
                LayoutWrapper
              : Fragment

          route.element = (
            <Wrapper {...(isBlank === false ? getRouteMeta(route) : {})}>
              <RouteTag route={route}>{route.element}</RouteTag>
            </Wrapper>
          )
        }

        // Push route to LayoutRoutes
        LayoutRoutes.push(route)
      }
      return LayoutRoutes
    })
  }
  return LayoutRoutes
}

const getRoutes = layout => {
  const defaultLayout = layout || 'vertical'
  const layouts = ['vertical', 'horizontal', 'blank']

  const AllRoutes = []

  layouts.forEach(layoutItem => {
    const LayoutRoutes = MergeLayoutRoutes(layoutItem, defaultLayout)

    AllRoutes.push({
      path: '/',
      element: getLayout[layoutItem] || getLayout[defaultLayout],
      children: LayoutRoutes
    })
  })
  return AllRoutes
}

export { DefaultRoute, TemplateTitle, Routes, getRoutes }
